@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR|ZCOOL+QingKe+HuangYou);
.about__container {
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  padding: 20px;
  border-radius: 5px;
  background-color: white;
  margin: 0 auto;
  margin-top: 100px;
  max-width: 400px;
  font-weight: 300;
}

.about__container span:first-child {
  font-size: 20px;
}

.about__container span:last-child {
  display: block;
  margin-top: 10px;
}

* {
  box-sizing: border-box;
}

.movie {
  margin: 0 auto;
  padding: 0;
  justify-content: center;
}

.movieCard {
  position: relative;
  margin: 20px 10px;
  max-width: 100%;
  /*padding: 8px;*/
}
.movie-poster {
  position: relative;
  width: 250px;
  height: 350px;
}
.movieimg {
  width: 250px;
  height: 350px;
  border-radius: 10px;
}
.movie-poster:hover + .mv-inf {
  visibility: visible;
}

.mv-inf:hover {
  visibility: visible;
}

.mv-inf {
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 0;
  padding: 8px;
  width: 250px;
  align-self: end;
  background-color: white;
  border-radius: 0 0 8px 8px;
  /*margin: 8px;*/
  visibility: hidden;
  transition: visibility 500ms;
}

.mv-inf-top {
  text-align: left;
  font-weight: bolder;
}

.mv-inf-btm {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
}
.mv-inf-overview {
  display: flex;
  flex-wrap: wrap;
  text-align: left;
}

.mv-inf-vote-avg {
  display: flex;
  flex-direction: column;
  /*justify-content: center;*/
  border-radius: 8px;
  background-color: red;
  /*padding-left:8px;*/
  /*padding-right: 8px;*/
  padding: 4px;
  color: white;
  /*height: 22px;*/
  /*align-self: center;*/
  text-align: center;
}

.mv-inf-vote-avg .star {
  margin: 0 auto;
  color: yellow;
}
@media only screen and (max-width: 600px) {
  .movie-poster {
    position: relative;
    width: 125px;
    height: 175px;
  }
  .movieimg {
    width: 125px;
    height: 175px;
    border-radius: 10px;
  }

  .movieCard {
    margin: 0;
  }

  .mv-inf {
    width: 125px;
    font-size: 12px;
  }

  .mv-inf-btm {
    font-size: 10px;
  }
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.movies {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(auto, 260px));
  grid-gap: 0.5%;
  padding: 3%;
  width: 80%;
  padding-top: 70px;
  justify-content: center;
}

@media screen and (max-width: 600px) {
  .movies {
    grid-template-columns: repeat(2, minmax(auto, 150px));
    max-width: 100%;
  }
}

:root {
  --text-color: #f0f4f5;
  --background-color: #263343;
  --accent-color: #d49466;
}

body {
  margin: 0;
  font-family: "Noto Sans KR";
}

.nav a {
  text-decoration: none;
  color: #f0f4f5;
  color: var(--text-color);
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #263343;
  background-color: var(--background-color);
  padding: 8px 12px;
  color: #f0f4f5;
  color: var(--text-color);
}

.navbar__logo {
  font-size: 24px;
  color: #f0f4f5;
  color: var(--text-color);
}
.navbar__logo a {
  font-size: 24px;
  margin-left: 5px;
}

.navbar__logo .film {
  color: #d49466;
  color: var(--accent-color);
}

.navbar__menuhidden {
  display: flex;
  list-style: none;
  padding-left: 0;
}

.navbar__menuhidden li {
  padding: 8px 12px;
}
.navbar__menuhidden li:hover {
  background-color: #d49466;
  background-color: var(--accent-color);
  border-radius: 4px;
}

.navbar__iconshidden {
  list-style: none;
  color: #f0f4f5;
  color: var(--text-color);
  display: flex;
  padding-left: 0;
}

.navbar__iconshidden li {
  padding: 8px 12px;
}

.navbar__toogleBtn {
  display: none;
  position: absolute;
  right: 32px;
  font-size: 24px;
  color: #d49466;
  color: var(--accent-color);
}

@media screen and (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 8px 24px;
  }

  .navbar__menushow {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .navbar__menu li {
    width: 100%;
    text-align: center;
  }

  .navbar__iconsshow {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .navbar__toogleBtn {
    display: flex;
    position: absolute;
    right: 32px;
    margin-top: 8px;
  }
  .navbar__toogleBtn element.style {
    position: absolute;
  }

  element .navbar__toogleBtn {
    position: absolute;
  }

  .navbar__menuhidden,
  .navbar__iconshidden {
    display: none;
  }
}

* {
  margin: 0;
  padding: 0;
}

.details .movieDetail {
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  background-color: white;
  max-width: 75%;
  max-height: 85%;
  margin-left: 10%;
  margin-right: 10%;
  margin-bottom: 70px;
  font-weight: 300;
  border-radius: 5px;
  margin-top: 15%;
  height: 100%;
  width: 100%;
}

a {
  margin-top: -5%;
  text-decoration: none;
  color: black;
}

.movieDetail .movieDetail__data .movieDetail__title h4 {
  font-weight: 150;
}

.movieDetail img {
  position: relative;
  max-width: 200px;
  width: 100%;
  margin-right: 30px;
  margin-left: 30px;
  top: -50px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movieDetail__production {
  margin-top: 20px;
}

.movieDetail .movieDetail__companies img {
  max-width: 75px;
  width: 100%;
  margin-top: 60px;
  box-shadow: none;
  align-items: center;
  margin-bottom: 10px;
}

.movieDetail .movieDetail__companies .companies__company {
  text-align: center;
}

.details .movieDetail__data {
  width: 75%;
  height: 75%;
  padding: 10px;
  text-align: left;
}

html > body #content {
  width: 370px;
}

ul {
  list-style-type: none;
  display: inline;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0px;
}
li {
  margin-right: 3px;
}

@media (max-width: 670px) {
  .movieDetail img {
    max-width: 100px;
    width: 100%;
    margin-bottom: -40px;
  }

  .movieDetail .movieDetail__data {
    width: 100%;
    margin-right: 0;
  }
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans KR", sans-serif;
  background-color: #eff3f7;
  height: 100%;
}

